<script>
var jwtDecode = require("jwt-decode");
import axios from "axios";
export default {
     name: "Signup",
     data() {
          return {
               email: "",
               firstName: "",
               lastName: "",
               username: "",
               password: "",
               confirmPassword: "",
               landlord: false
          };
     },
     methods: {
          valid() {
               $("#signup_email")[0].setCustomValidity("");
               $("#signup_firstName")[0].setCustomValidity("");
               $("#signup_lastName")[0].setCustomValidity("");
               $("#signup_username")[0].setCustomValidity("");
               if (this.email == "" || !($("#signup_email")[0].checkValidity())) {
                    $("#signup_email")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: email is invalid.");
                    return false;
               } else if (
                    this.firstName == "" ||
                    !$("#signup_firstName")[0].checkValidity()
               ) {
                    $("#signup_firstName")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: first name is invalid.");
                    return false;
               } else if (
                    this.lastName == "" ||
                    !$("#signup_lastName")[0].checkValidity()
               ) {
                    $("#signup_lastName")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: last name is invalid.");
                    return false;
               } else if (
                    this.username == "" ||
                    !$("#signup_username")[0].checkValidity()
               ) {
                    $("#signup_username")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: username is invalid.");
                    return false;
               } else if (this.password.length < 10) {
                    $("#signup_password")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: password is invalid.");
                    return false;
               } else if (this.password != this.confirmPassword) {
                    $("#signup_confirmPassword")[0].setCustomValidity("invalid");
                    $("#signup_error").text("Error: passwords do not match");
                    return false;
               }   
               return true;
          },
          signupRequest() {
               if (this.valid()) {
                    $("#signup_submit").attr("disabled", "disabled");
                    $("#signup_error").text("...");
                    var type = "USERTENANTACCOUNT";
                    if (this.landlord == true) type = "USERLEASERACCOUNT";
                    var form = $("#signup_form");
                    var formData = {
                         email: this.email,
                         username: this.username,
                         accountType: type,
                         firstName: this.firstName,
                         lastName: this.lastName,
                         password: this.password
                    };
                    axios({
                         method: "post",
                         url: this.$store.get("apiUrl") + "users/register",
                         data: formData,
                         headers: {
                              withCredentials: true
                         }
                    })
                         .then(response => {
                              this.$store.set("token", response.data.encoded);
                              this.$store.set(
                                   "username",
                                   response.data.username
                              );
                              this.$router.push("home/");
                         })
                         .catch(function(error) {
                              $("#signup_submit").removeAttr("disabled");
                              $("#signup_error").text(error.response.data.detail);
                         });
               }
          }
     },
     computed: {},
     components: {},
     mounted() {}
};
</script>

